.profile{
     display: flex;
}

.profileCover{
     height: 320px;
     position: relative;
}

.profileRight{
     flex: 9;
}

.profileCoverImg{
     width: 100%;
     height: 250px;
     object-fit: cover;
     object-position: center;
}
.profileImg{
     width: 150px;
     height: 150px;
     border-radius: 50%;
     object-fit: cover;
     position: absolute;
     left: 0;
     right: 0;
     margin: auto;
     top: 150px;
     border: 3px solid white;
}

.profileInfo{
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
}

.profileName{
     font-size: 24px;
}

.profileDesc{
     font-weight: 300;
}



.profileRightBottom{
     display: flex;
}
